"use client"

import { useState } from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import toast from "react-hot-toast"

const CreateStreamCategory = () => {
  const [name, setName] = useState("")
  const [error, setError] = useState("")

  const handleChange = (e) => {
    const value = e.target.value
    // First letter capitalize
    const capitalized =
      value.charAt(0).toUpperCase() + value.slice(1).toLowerCase()
    setName(capitalized)
    if (capitalized.trim() !== "") {
      setError("")
    }
  }

  const handleSubmit = async (e) => {
    e.preventDefault()

    if (!name.trim()) {
      setError("Category name is required.")
      return
    }

    try {
      const token = localStorage.getItem("token")
      if (!token) throw new Error("Token not found")

      const res = await fetch(
        "https://wiqiapi.testenvapp.com/api/admin/streamCategory",
        {
          method: "POST",
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ name }), // âœ… Correct: send { "name": "Tv" }
        }
      )

      const data = await res.json()

      if (res.ok) {
        toast.success(data.message || "Stream category created successfully!")
        setName("")
      } else {
        toast.error(data.message || "Failed to create category.")
      }
    } catch (err) {
      toast.error("Something went wrong!")
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Create Stream Category</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <Label htmlFor="name">Category Name</Label>
            <Input
              id="name"
              placeholder="Enter category name"
              value={name}
              onChange={handleChange}
            />
            {error && (
              <p className="text-red-500 text-sm mt-1">{error}</p>
            )}
          </div>
          <Button type="submit" className="w-full">
            Create
          </Button>
        </form>
      </CardContent>
    </Card>
  )
}

export default CreateStreamCategory
